.footer-content {
  background-color: #f25c05; /* Bright orange */
  color: #fff;
  padding: 40px 0;
  font-family: 'Roboto', sans-serif;
}

/* We apply margin-bottom on smaller screens below */
.footer-col {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
}

/* Branding */
.footer-brand {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #fff;
}

.footer-tagline {
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
}

/* Section Titles */
.footer-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #fff;
}

/* Quick Links */
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 10px;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #ffe3d0;
  text-decoration: underline;
}

/* Social Icons */
.footer-social-icons {
  display: flex;
  gap: 15px;
  margin-top: 1rem;
}

.social-icon {
  font-size: 1.5rem;
  color: #fff;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-icon:hover {
  color: #ffe3d0;
  transform: scale(1.2);
}

/* Divider */
.footer-divider {
  border: none;
  height: 2px;
  background-color: #fff;
  margin: 30px 0;
  opacity: 0.2;
}

/* Copyright */
.footer-content p {
  margin: 0;
  font-size: 0.9rem;
  opacity: 0.9;
}

/*******************************************************
  RESPONSIVE
*******************************************************/
/* On screens >= 768px, we keep three columns in one row.
   For smaller screens, each .footer-col becomes a vertical column. */
@media (max-width: 768px) {
  .footer-top-row {
    flex-direction: column;
    align-items: center;  /* centers each col horizontally */
  }

  /* Add extra spacing between stacked columns */
  .footer-col:not(:last-child) {
    margin-bottom: 2rem;
  }
}
