/************************************************
  TEAM SECTION LAYOUT
************************************************/
.team-section {
    max-width: 1200px;
    margin: 0 auto;
    background-color: #fff; /* White background to contrast orange header & footer */
    padding: 2rem 1rem;
  }
  
  .team-section h2 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #333;
  }
  
  .section-intro {
    text-align: center;
    max-width: 700px;
    margin: 0 auto 2rem auto;
    font-size: 1.1rem;
    color: #555;
    line-height: 1.6;
  }
  
  /************************************************
    TEAM MEMBER LAYOUT
  ************************************************/
  .team-member {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    border-bottom: 1px solid #ddd;
    padding-bottom: 2rem;
    gap: 2rem; /* spacing between image & info */
  }
  
  .team-image {
    flex: 1 1 250px;
    text-align: center;
  }
  
  .team-image img {
    max-width: 100%;
    border-radius: 6px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .team-info {
    flex: 2 1 300px;
  }
  
  .team-info h3 {
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    color: #f25c05; /* Use brand orange for names */
  }
  
  .team-info p {
    color: #333;
    line-height: 1.6;
    margin: 0;
  }
  
  /************************************************
    RESPONSIVE STYLES
  ************************************************/
  @media (max-width: 768px) {
    .team-member {
      flex-direction: column;
      align-items: center;
    }
  
    .team-image,
    .team-info {
      flex: 1 1 100%;
      text-align: center;
    }
  
    .team-info h3 {
      margin-top: 1rem;
    }
  }
  