/*******************************************************
  BASIC HEADER STYLES
*******************************************************/
.site-header {
    background-color: #f25c05; /* Brand color */
    padding: 1rem;
  }
  
  .header-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .logo h1 {
    font-size: 1.8rem;
    font-weight: bold;
    color: #fff;
    margin: 0;
  }
  
  /*******************************************************
    NAV TOGGLE (HAMBURGER ICON)
  *******************************************************/
  .nav-toggle {
    font-size: 1.5rem;
    color: #fff;
    background: none;
    border: none;
    cursor: pointer;
    display: none; /* hidden by default, shown on mobile */
  }
  
  /*******************************************************
    NAVIGATION MENU
  *******************************************************/
  .top-nav {
    display: flex;
    align-items: center;
  }
  
  .top-nav ul {
    list-style: none;
    display: flex;
    gap: 1.5rem;
    margin: 0;
    padding: 0;
  }
  
  .top-nav li {
    margin: 0;
  }
  
  .top-nav a {
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 600;
    transition: color 0.3s;
  }
  
  .top-nav a:hover {
    color: #ffe3d0; /* lighter shade on hover */
    text-decoration: underline;
  }
  
  /*******************************************************
    RESPONSIVE STYLES
  *******************************************************/
  /* On smaller screens, show hamburger and hide default nav layout */
/* ... existing styles ... */

/*******************************************************
  RESPONSIVE STYLES
*******************************************************/
@media (max-width: 768px) {
    .nav-toggle {
      display: block; /* Show hamburger on mobile */
    }
  
    /* By default, hide the nav on mobile unless active */
    .top-nav {
      position: absolute;
      top: 4.5rem; /* just below the header */
      right: 1rem;
      background-color: #f25c05;
      flex-direction: column;
      gap: 1rem;
      padding: 1rem;
      border-radius: 4px;
      display: none; /* hidden by default */
      z-index: 9999; /* Ensure menu is on top of other elements */
    }
  
    /* When .active is added, show the nav */
    .top-nav.active {
      display: flex;
    }
  
    .top-nav ul {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  
  