/************************************************
  GLOBAL STYLES
************************************************/
body, html, main {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

/************************************************
  HERO SECTION
************************************************/
/************************************************
  HERO SECTION
************************************************/
.hero-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  background-color: #f9f9f9;
  padding: 2rem 1rem; /* Reduced from 3rem 2rem to shrink vertical space */
  margin-top: 0; 
}

.hero-text {
  flex: 1 1 400px;
  margin-right: 2rem;
}

/* Reduce the top margin on the H2 */
.hero-text h2 {
  font-size: 2.2rem;
  margin-bottom: 0.75rem; /* was 1rem, make it slightly smaller */
  margin-top: 0;         /* remove any top margin the heading might have */
  color: #333;
  line-height: 1.2;
}

.hero-text p {
  font-size: 1.1rem;
  margin-bottom: 1rem; /* was 1.5rem, also reduce a bit */
  color: #555;
  max-width: 600px;
}

.cta-button {
  background-color: #f25c05;
  color: #fff;
  border: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 4px;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.3s ease;
  margin-top: 1rem; /* ensures button isn't jammed against the text */
}

.cta-button:hover {
  background-color: #d84b03;
}

.hero-image {
  flex: 1 1 300px;
  text-align: center;
  margin-top: 1rem; /* or remove if you donâ€™t want spacing above the image */
}

.hero-image img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

/************************************************
  PHILOSOPHY SECTION
************************************************/
.philosophy-section {
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  padding: 3rem 2rem; /* Extra spacing */
}

.philosophy-content {
  flex: 1 1 400px;
  margin-right: 2rem;
}

.philosophy-content h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #333;
}

.philosophy-content p {
  margin-bottom: 1rem;
  font-size: 1.1rem;
  color: #555;
  max-width: 600px; /* Limit width for better readability */
  line-height: 1.6;
}

.read-more {
  display: inline-block;
  margin-top: 1rem;
  background-color: #f25c05; /* Brand orange */
  color: #fff;
  text-decoration: none;
  padding: 0.7rem 1.4rem;
  border-radius: 4px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.read-more:hover {
  background-color: #d84b03; /* Slightly darker on hover */
}

.philosophy-image {
  flex: 1 1 300px;
  text-align: center;
  margin-top: 1.5rem;
}

.philosophy-image img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

/************************************************
  RESPONSIVE STYLES
************************************************/
@media (max-width: 768px) {
  .hero-section, 
  .philosophy-section {
    flex-direction: column;
    padding: 2rem 1rem; /* Slightly less padding on smaller screens */
  }

  .hero-text, 
  .philosophy-content {
    margin-right: 0;
    margin-bottom: 1.5rem;
    text-align: center; /* Center text on mobile for a cleaner look */
  }

  .hero-text h2,
  .philosophy-content h2 {
    font-size: 1.8rem;
  }

  .hero-text p,
  .philosophy-content p {
    max-width: 100%; /* Let text fill the screen on mobile */
  }

  .hero-image, 
  .philosophy-image {
    margin-top: 1rem;
  }
}
