/******************************************************
   GENERAL PAGE STYLES
******************************************************/
.philosophy-page {
    font-family: Arial, sans-serif;
  }
  
  /******************************************************
     CAROUSEL SECTION
  ******************************************************/
  .carousel-section {
    margin-bottom: 2rem;
    position: relative;
    overflow: hidden;
  }
  
  .carousel-img {
    height: 500px; /* Adjust as needed */
    object-fit: cover;
    object-position: center;
  }
  
  /* Customize carousel captions */
  .carousel-caption-custom {
    background-color: rgba(242, 92, 5, 0.8); /* Orange overlay with opacity */
    padding: 1rem;
    border-radius: 4px;
  }
  
  .carousel-caption-custom h3,
  .carousel-caption-custom p {
    color: #fff;
    margin: 0.2rem 0;
  }
  
  /******************************************************
     PHILOSOPHY SECTION
  ******************************************************/
  .philosophy-page-section {
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem 1rem;
  }
  
  .philosophy-page-section h2 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #f25c05; /* Emphasize brand orange for headings */
    font-weight: 700;
  }
  
  .section-intro {
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
    text-align: center;
    color: #555;
  }
  
  .philosophy-details p {
    margin-bottom: 1rem;
    line-height: 1.6;
    font-size: 1rem;
    color: #333;
  }
  
  /******************************************************
     SERVICES SECTION
  ******************************************************/
  .services-section {
    margin-top: 2rem;
    border-left: 4px solid #f25c05;
    padding-left: 1rem;
    margin-bottom: 2rem;
  }
  
  .services-section h3 {
    font-size: 1.4rem;
    color: #f25c05;
    margin-bottom: 1rem;
    font-weight: 700;
  }
  
  .services-section ul {
    list-style: disc;
    margin-left: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .services-section li {
    margin-bottom: 0.5rem;
    color: #333;
  }
  
  /******************************************************
     RESPONSIVE ADJUSTMENTS
  ******************************************************/
  @media (max-width: 768px) {
    .carousel-img {
      height: auto;
    }
  
    .philosophy-page-section {
      padding: 1rem;
    }
  }
  